import React, { useState, useEffect, useRef } from "react";
import icons from "../../assets/image/icons.svg";
import { Link } from "react-router-dom";
import person1 from "../../assets/image/other/person.png";
import person2 from "../../assets/image/other/person2 (1).png";
import person3 from "../../assets/image/other/person3.png";

function RoomsItem() {
  const [open, setOpen] = useState(false);
  const [show, setShow] = useState(false);
  const ref = useRef(null);

  useEffect(() => {
    function handleClickOutside(event) {
      if (ref.current && !ref.current.contains(event.target)) {
        setShow(false);
      }
    }
    document.addEventListener("click", handleClickOutside);
    return () => {
      document.removeEventListener("click", handleClickOutside);
    };
  }, [ref]);
  return (
    <div className="rooms-content">
      <div className="rooms-content__head">
        <div className="rooms-content__emojy">
          <span role="img" aria-label="😒">
            😒
        </span>
        </div>

        <h2>Good Mornings</h2>

        <button>
          <svg className="icon icon-bell-o">
            <use href={icons + "#icon-bell-o"}></use>
          </svg>
        </button>


      </div>

      <div className={`rooms-content__center ${show && "show"}`}>
        <div className="rooms-persons">
          <div className="rooms-persons__hide" onClick={() => setShow(false)}>
            <svg className="icon icon-up-angle">
              <use href={icons + "#icon-up-angle"}></use>
            </svg>
          </div>
          <div className="rooms-persons__body">
            <ul className="rooms-persons__list">
              <li className="rooms-persons__item">
                <img src={person2} alt="" />
                <span>Valeh Huseynov</span>
              </li>
              <li className="rooms-persons__item">
                <img src={person2} alt="" />
                <span>Valeh Huseynov</span>
              </li>
            </ul>
          </div>
        </div>
      </div>

      {!show && (
        <div className="rooms-content__bottom" ref={ref}>
          <h2 className="rooms-content__title">Participants</h2>
          <div className="rooms-content__images">
            <img src={person1} alt="" />
            <img src={person1} alt="" />
            <img src={person1} alt="" />
            <img src={person1} alt="" />
            <img src={person1} alt="" />
            <div className="rooms-content__more" onClick={() => setShow(true)}>
              <span>+10</span>
            </div>
          </div>
        </div>
      )}
      <div className="rooms-settings">
        <button><svg className="icon icon-users"><use href={icons + "#icon-users"}></use></svg><span>Invite People</span></button>
        <button><svg className="icon icon-content_copy"><use href={icons + "#icon-content_copy"}></use></svg><span>Copy Link</span></button>
        <button onClick={() => setOpen(!open)} className="rooms-settings__tool" >
          <svg className="icon icon-dost">
            <use href={icons + "#icon-dost"}></use>
          </svg>
        </button>
        {open && (
          <div className="rooms-tools">
            <div className="rooms-tools__body">
              <div className="rooms-tools__content">
                <p onClick={() => setOpen(!open)}>
                  <Link to="/rooms/create">Edit</Link>
                </p>
                <p onClick={() => setOpen(!open)}>Mute</p>
                <p onClick={() => setOpen(!open)}>Remove</p>
              </div>
            </div>
          </div>
        )}
      </div>
      <div className="rooms-join">
        <button>Join Room</button>
      </div>

    </div >
  );
}

export default RoomsItem;
