import React from "react";
import { useHistory } from "react-router-dom";
import "./Aside.css";
import AsideNav from "./AsideNav";
import { Link } from "react-router-dom";
import logo from "../../assets/image/logo/LogoWhite.png";
import icons from "../../assets/image/icons.svg";
import * as requests from '../../api/requests';

function Aside() {

  let history = useHistory();

  function logout(){
    requests.setToken('');
    history.push('/signin');
  }

  return (
    <aside className="aside">
      <div className="aside-body">
        <div className="aside-logo">
          <Link to="/dashboard">
            <img src={logo} alt="Logo" />
          </Link>
        </div>
        <AsideNav />
        <div className="aside-bottom">
          <a href="javascript:void(0)" onClick={(e) => logout()}>
            <svg className="icon icon-exittoapp_baseline">
              <use href={icons + "#icon-exittoapp_baseline"}></use>
            </svg>
          </a>
        </div>
      </div>
    </aside>
  );
}

export default Aside;
