import React, { useEffect, useState } from "react";
import "./NotificationItem.css";
import person from "../../assets/image/other/person@2x.png";
import icons from "../../assets/image/icons.svg";
import * as requests from "../../api/requests";
import * as userService from "../../api/users";
import * as messagingService from "../../api/messaging";
import * as filesService from "../../api/files";
const electron = window.require("electron").remote;

function NotificationList({ user, time, code }) {

  const [activeusers, setActiveusers] = useState([]);
  const [avatarurl, setAvatarurl] = useState('');

  useEffect(() => {
    let activeUsers = messagingService.getActiveUsers();
    setActiveusers([...activeUsers]);

    if(user.avatar && !avatarurl){
      filesService.getFile(user.avatar).then((res) => res.json())
      .then((data) => {
        setAvatarurl(data);
      });
    }
  }, []);



  function getNotificationType(){
    let type = '';
    switch (code) {
      case 'user.video-call':
        // statements_1
        type = 'Video call';
        break;
      case 'user.audio-call':
        // statements_1
        type = 'Audio call';
        break;
      case 'user.request-share':
        // statements_1
        type = 'Share request';
        break;
      case 'ntf.ht.requestAccepted':
        type = 'New team member';
        break;
    }

    return type;
  }

  function getNotificationTime(){
    let d = new Date(time);
    return (d.getHours() < 10 ? '0' + d.getHours() : d.getHours()) + ':' + (d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes());
  }

  function getUserStatus(){
    let status = '';
    let userInfo = activeusers.find(u => u.id == user.id);

    if(userInfo){
      return userInfo.inmeeting ? 'meeting' : 'online';
    } else {
      return status;
    }
  }

  function clickOnNotification(){
    let u, noti;
    switch (code) {
      case 'user.video-call':
        // statements_1
        u = userService.getUserInfo();
        u.uType = u.type;
        noti = {
          code: "user.video-call",
          actingUser: u,
          otherUser: { id: user.id },
          involved: [user.id]
        };

        messagingService
          .addNotification(noti)
          .then((res) => res.json())
          .then((data) => {
            let fromId = u.id;

            let path = electron.getGlobal('sharedPath'); 
            let filePath = electron.getGlobal('filePath'); 

            const BrowserWindow = electron.BrowserWindow;
              let callWindow = new BrowserWindow({
              width: 300,
              height: 500,
              title: "Wteam call",
              frame: false,
              transparent: true,
              resizable: false,
              webPreferences : { 
                preload: filePath + '../public/preload.js',
                nodeIntegration: true,
                enableRemoteModule: true,
                webSecurity: false
              }
            });
            callWindow.loadURL(path + '../call/call.html?roomId=' + user.id +
                "&fromId=" +
                fromId +
                "&token=" +
                requests.getToken());
          })
          .catch((err) => {
            console.log("members error: ", err);
          });
        break;
      case 'user.audio-call':
        // statements_1
        u = userService.getUserInfo();
        u.uType = u.type;
        noti = {
          code: "user.audio-call",
          actingUser: u,
          otherUser: { id: user.id },
          involved: [user.id]
        };

        messagingService
          .addNotification(noti)
          .then((res) => res.json())
          .then((data) => {
            let fromId = u.id;

            let path = electron.getGlobal('sharedPath'); 
            let filePath = electron.getGlobal('filePath'); 

            const BrowserWindow = electron.BrowserWindow;
              let callWindow = new BrowserWindow({
              width: 300,
              height: 500,
              title: "Wteam call",
              frame: false,
              transparent: true,
              resizable: false,
              webPreferences : { 
                preload: filePath + '../public/preload.js',
                nodeIntegration: true,
                enableRemoteModule: true,
                webSecurity: false
              }
            });
            callWindow.loadURL(path + '../call/call.html?onlyAudio=true&roomId=' + user.id +
                "&fromId=" +
                fromId +
                "&token=" +
                requests.getToken());
          })
          .catch((err) => {
            console.log("members error: ", err);
          });
        break;
      case 'user.request-share':
        // statements_1
        u = userService.getUserInfo();
        u.uType = u.type;
        noti = {
          code: "user.video-call",
          actingUser: u,
          otherUser: { id: user.id },
          involved: [user.id]
        };

        messagingService
          .addNotification(noti)
          .then((res) => res.json())
          .then((data) => {
            let fromId = u.id;

            let path = electron.getGlobal('sharedPath'); 
            let filePath = electron.getGlobal('filePath'); 

            const BrowserWindow = electron.BrowserWindow;
              let callWindow = new BrowserWindow({
              width: 300,
              height: 500,
              title: "Wteam call",
              frame: false,
              transparent: true,
              resizable: false,
              webPreferences : { 
                preload: filePath + '../public/preload.js',
                nodeIntegration: true,
                enableRemoteModule: true,
                webSecurity: false
              }
            });
            callWindow.loadURL(path + '../call/call.html?roomId=' + user.id +
                "&fromId=" +
                fromId +
                "&token=" +
                requests.getToken());
          })
          .catch((err) => {
            console.log("members error: ", err);
          });
        break;
    }
  }

  return (
    <div className="notification-item" onClick={() => clickOnNotification()}>
      {avatarurl && (
              <div className="notification-item__image">
                <img src={avatarurl} alt="Person" />
              </div>
            )}
            {!avatarurl && (
              <div className="notification-item__image notification-item__image--name">
                <p>
                  {user.name.first.charAt(0)}
                  {user.name.last.charAt(0)}
                </p>
              </div>
            )}
      <div className="notification-item__name notification-item__text ">
        <span>{user.name.first}</span>
      </div>
      <div className="notification-item__status notification-item__text ">
        <svg
          className={`icon icon-circul ${
            getUserStatus()
          }`}
        >
          <use href={icons + "#icon-circul"}></use>
        </svg>
        <span>{getUserStatus() == 'meeting' ? "In Meeting" : getUserStatus() == 'online' ? "Active Now" : "Offline"}</span>
      </div>
      <div className="notification-item__type notification-item__text ">
        <svg className="icon icon-call-missed">
          <use href={icons + "#icon-call-missed"}></use>
        </svg>
        <span>{getNotificationType()}</span>
      </div>
      <div className="notification-item__time notification-item__text ">
        <span>{getNotificationTime()}</span>
      </div>
      <div className="notification-item__delete">
        <button>
          <svg className="icon icon-cross">
            <use href={icons + "#icon-cross"}></use>
          </svg>
        </button>
      </div>
    </div>
  );
}

export default NotificationList;
