import React, { useState, useRef, useEffect } from "react";
import icons from "../../assets/image/icons.svg";
import appicons from "../../assets/image/appicons.svg";
import person from "../../assets/image/other/person@2x.png";
import doc from "../../assets/image/other/doc.png";
import "./Teammates.css";
import * as requests from "../../api/requests";
import * as userService from "../../api/users";
import * as messagingService from "../../api/messaging";
import * as filesService from "../../api/files";
const electron = window.require("electron").remote;

const appIconsMap = {
  'adobe animate': 'adobe-animate',
  'adobe illustrator': 'adobe-illustrator-cc',
  'adobe xd': 'adobe-xd-1',
  'airtable': 'airtable',
  'asana': 'asana-logo',
  'atom': 'atom-4',
  'aws': 'aws-2',
  'basecamp': 'basecamp',
  'bitbucket': 'bitbucket',
  'blender': 'blender-2',
  'buffer': 'buffer',
  'digitalocean': 'digitalocean-icon-1',
  'dropbox' : 'dropbox-1',
  'dropbox paper': 'dropbox-paper',
  'edge animate': 'edge-animate-app-cc',
  'electron': 'electron',
  'figma' : 'figma-1',
  'front': 'front',
  'github': 'github-1',
  'gitlab': 'gitlab',
  'google slide': 'google slide',
  'google calendar': 'google-calendar',
  'google docs': 'google-docs-icon',
  'google meet': 'google-meet-2',
  'google sheets': 'google-sheets',
  'hubspot': 'hubspot-1',
  'ibooks': 'ibooks',
  'indesign': 'indesign-cc',
  'intercom': 'intercom',
  'invision': 'invision',
  'jira': 'jira',
  'linkedin': 'linkedin-icon-2',
  'microsoft excel': 'microsoft-excel-2013',
  'microsoft powerpoint': 'microsoft-powerpoint-2013',
  'microsoft word': 'microsoft-word-2013-logo',
  'notion': 'notion-2',
  'numbers': 'numbers',
  'onedrive': 'onedrive-1',
  'pages ios': 'pages-ios',
  'photoshop': 'photoshop-cc',
  'premiere': 'premiere-cc',
  'salesforce': 'salesforce-2',
  'sketch': 'sketch-2',
  'skype': 'skype-1',
  'slack': 'slack',
  'stack overflow': 'stack-overflow',
  'teamwork': 'teamwork-2',
  'telegram': 'telegram-1',
  'terminal': 'terminal',
  'trello': 'trello',
  'unity': 'unity-69',
  'visual studio 2013': 'visual-studio-2013',
  'visual studio code': 'visual-studio-code',
  'zendesk': 'zendesk-1',
  'zeplin': 'zeplin',
  'zoom': 'zoom-communications-logo'
};

export default function Teammates({ activeUsers, name, time, lastname, list, id, avatar, shareApp, shareAppUrl }) {
  const [tools, setTools] = useState(false);
  const [chatShow, setChatShow] = useState(false);
  const [avatarurl, setAvatarurl] = useState('');


  const ref = useRef(null);


  useEffect(() => {
    console.log('avatar, avatar-url: ', avatar, avatarurl);
    if(avatar && !avatarurl){
      filesService.getFile(avatar).then((res) => res.json())
      .then((data) => {
        setAvatarurl(data);
        console.log('data: ', data);
      });
    }
  }, []);


  function startVideoCall(id) {
    console.log("user id: ", id);
    let user = activeUsers.find((user) => user.id == id);
    if(user && !user.inmeeting && user.appStatus != 'Focus mode'){
      let user = userService.getUserInfo();
      user.uType = user.type;
      let noti = {
        code: "user.video-call",
        actingUser: user,
        otherUser: { id: id },
        involved: [id]
      };

      

      messagingService
        .addNotification(noti)
        .then((res) => res.json())
        .then((data) => {
          let fromId = user.id;

          let path = electron.getGlobal('sharedPath'); 
          let filePath = electron.getGlobal('filePath'); 

          const BrowserWindow = electron.BrowserWindow;
            let callWindow = new BrowserWindow({
            width: 300,
            height: 500,
            title: "Wteam call",
            frame: false,
            transparent: true,
            resizable: false,
            webPreferences : { 
              preload: filePath + '../public/preload.js',
              nodeIntegration: true,
              enableRemoteModule: true,
              webSecurity: false
            }
          });
          callWindow.loadURL(path + '../call/call.html?roomId=' + id +
              "&fromId=" +
              fromId +
              "&token=" +
              requests.getToken());
        })
        .catch((err) => {
          console.log("members error: ", err);
        });
    }
  }

  function startAudioCall(id) {
    console.log("user id: ", id);
    let user = activeUsers.find((user) => user.id == id);
    if(user && !user.inmeeting && user.appStatus != 'Focus mode'){
      let user = userService.getUserInfo();
      user.uType = user.type;
      let noti = {
        code: "user.audio-call",
        actingUser: user,
        otherUser: { id: id },
        involved: [id]
      };

      

      messagingService
        .addNotification(noti)
        .then((res) => res.json())
        .then((data) => {
          let fromId = user.id;

          let path = electron.getGlobal('sharedPath'); 
          let filePath = electron.getGlobal('filePath'); 

          const BrowserWindow = electron.BrowserWindow;
            let callWindow = new BrowserWindow({
            width: 300,
            height: 500,
            title: "Wteam call",
            frame: false,
            transparent: true,
            resizable: false,
            webPreferences : { 
              preload: filePath + '../public/preload.js',
              nodeIntegration: true,
              enableRemoteModule: true,
              webSecurity: false
            }
          });
          callWindow.loadURL(path + '../call/call.html?onlyAudio=true&roomId=' + id +
              "&fromId=" +
              fromId +
              "&token=" +
              requests.getToken());
        })
        .catch((err) => {
          console.log("members error: ", err);
        });
    }
  }

  function startChat(id) {
    let fromId = userService.getUserInfo().id;
    let path = electron.getGlobal('sharedPath'); 
    let filePath = electron.getGlobal('filePath'); 

    const BrowserWindow = electron.BrowserWindow;
      let chatWindow = new BrowserWindow({
      width: 300,
      height: 500,
      title: "Wteam chat",
      frame: false,
      transparent: true,
      resizable: false,
      webPreferences : { 
        preload: filePath + '../public/preload.js',
        nodeIntegration: true,
        enableRemoteModule: true,
        webSecurity: false
      }
    });
    chatWindow.loadURL(path + '../chat/mainc.html?fromId=' + fromId +
        "&toId=" +
        id +
        "&token=" +
        requests.getToken());
  }

  function getUserStatusIcon(id) {
    let user = activeUsers.find((user) => user.id == id);
    return user && user.inmeeting ? 'meeting' : (user ? 'online' : '');
  }

  function getUserStatus(id) {
    let user = activeUsers.find((user) => user.id == id);
    let status = user && user.appStatus ? user.appStatus : undefined;
    return user && user.inmeeting ? 'meeting' : (user && status ? status : (user ? 'online' : ''));
  }

  function getUserUrl(id) {
    if(shareAppUrl){
      let url = (activeUsers.find((user) => user.id == id) && activeUsers.find((user) => user.id == id).activeWindow && activeUsers.find((user) => user.id == id).activeWindow.url) ? activeUsers.find((user) => user.id == id).activeWindow.url : (activeUsers.find((user) => user.id == id) && activeUsers.find((user) => user.id == id).tab ? activeUsers.find((user) => user.id == id).tab : '');

      return url && url.length ? true : false;
    } else {
      return false;
    }
    
  }

  function getUserAppIcon(id){
    let activeWindow = activeUsers.find((user) => user.id == id) && activeUsers.find((user) => user.id == id).activeWindow ? activeUsers.find((user) => user.id == id).activeWindow : null;
    let appIcon = '';
    for(let i in appIconsMap){
      if(activeWindow && ((activeWindow.name && activeWindow.name.toLowerCase().includes(i)) || (activeWindow.title && activeWindow.title.toLowerCase().includes(i)))){
        appIcon = appIconsMap[i];
        break;
      }
    }

    if(appIcon && shareApp){
      return appicons + "#" + appIcon;
    } else {
      return '';
    }
    
  }

  function goToDoc(id) {
    let url = (activeUsers.find((user) => user.id == id).activeWindow && activeUsers.find((user) => user.id == id).activeWindow.url) ? activeUsers.find((user) => user.id == id).activeWindow.url : (activeUsers.find((user) => user.id == id).tab ? activeUsers.find((user) => user.id == id).tab : '');
    
    if(url && shareAppUrl)
      window.open(url);
    else{
      let user = activeUsers.find((user) => user.id == id);
      if(user && !user.inmeeting && user.appStatus != 'Focus mode'){
        let user = userService.getUserInfo();
        user.uType = user.type;
        let noti = {
          code: "user.request-share",
          actingUser: user,
          otherUser: { id: id },
          involved: [id]
        };

      

        messagingService
          .addNotification(noti);
      }
    } 

  }

  return (
    <div className={`teammates ${list ? "list" : ""}`} ref={ref}>
      <div className={`teammates-body ${list ? "list" : ""}`}>
        <div className="teammates-body__grid">
          <div className="teammates-about" onClick={(e) => setTools(!tools)}>
            {avatarurl && (  
              <div className="teammates-about__image">
                <img src={avatarurl} alt="Person" />
              </div>
            )}
            {!avatarurl && (
              <div className="teammates-about__image teammates-about__image--name">
                <p>
                  {name.charAt(0)}
                  {lastname.charAt(0)}
                </p>
              </div>
            )}
            <div className="teammates-about__data">
              <div className="teammates-about__name">
                <h2>{name}</h2>

                {getUserAppIcon(id) && (
                  <a
                    href="javascript:void(0)"
                  >
                  <svg className="icon">
                    <use href={getUserAppIcon(id)}></use>
                  </svg>
                  </a>
                )}
              </div>
              <ul className="teammates-about__list">
                <li className="teammates-about__item">
                  <svg
                    className={`icon icon-circul ${
                      getUserStatusIcon(id)
                    }`}
                  >
                    <use href={icons + "#icon-circul"}></use>
                  </svg>
                  <span>{getUserStatus(id) == 'meeting' ? "In Meeting" : (getUserStatus(id).trim().length && getUserStatus(id) != 'online' ? getUserStatus(id) : (getUserStatus(id) == 'online' ? "Active now" : "Offline"))}</span>
                </li>
                <li className="teammates-about__item">
                  <svg className="icon icon-watch">
                    <use href={icons + "#icon-watch"}></use>
                  </svg>
                  <span>Time {time}</span>
                </li>
              </ul>
            </div>
          </div>

          <div className="teammates-bar">
            <div className="teammates-bar__item" onClick={(e) => startAudioCall(id)}>
              <div className={`teammates-bar__icon phone ${(getUserStatus(id) == "meeting" || getUserStatus(id) == 'Focus mode' || !getUserStatus(id)) ? "disabled" : ""}`}>
                <svg className="icon icon-phone1">
                  <use href={icons + "#icon-phone1"}></use>
                </svg>
              </div>
            </div>
            <div
              className="teammates-bar__item"
              onClick={(e) => startVideoCall(id)}
            >
              <div className={`teammates-bar__icon video ${(getUserStatus(id) == "meeting" || getUserStatus(id) == 'Focus mode' || !getUserStatus(id)) ? "disabled" : ""}`}>
                <svg className="icon icon-video2">
                  <use href={icons + "#icon-video2"}></use>
                </svg>
              </div>
            </div>
            <div
              className="teammates-bar__item"
              onClick={(e) => {
                startChat(id);
              }}
            >
              <div className="teammates-bar__icon chat">
                <svg className="icon icon-message-square">
                  <use href={icons + "#icon-message-square"}></use>
                </svg>
              </div>
            </div>
            {getUserAppIcon(id) && (
              <div
                className="teammates-bar__item doc"
                onClick={() => {
                  goToDoc(id);
                }}
              >
              {getUserUrl(id) && (
                <>
                <div className="teammates-bar__icon ">
                  <svg className="icon icon-link">
                    <use href={icons + "#icon-link"}></use>
                  </svg>
                </div>
                <div className="teammates-bar__doc">
                  <span>Presentaion documents</span>
                </div>
                </>
                )}

                {!getUserUrl(id) && (
                <>
                <div className="teammates-bar__icon ">
                  <svg className="icon icon-link">
                    <use href={icons + "#right_down_arrow"}></use>
                  </svg>
                </div>
                <div className="teammates-bar__doc">
                  <span>Request share</span>
                </div>
                </>
                )}
              </div>
            )}
          </div>
        </div>

        <div className="teammates-body__list">
          <div className="teammates-list">
          {avatarurl && (  
              <div className="teammates-list__image">
                <img src={avatarurl} alt="Person" />
              </div>
            )}
            {!avatarurl && (
              <div className="teammates-list__image teammates-list__image--name">
                <p>
                  {name.charAt(0)}
                  {lastname.charAt(0)}
                </p>
              </div>
            )}

            <div className="teammates-list__name">
              <span>{name}</span>
            </div>
            <div className="teammates-list__status">
              <svg
                className={`icon icon-circul ${
                  getUserStatusIcon(id)
                }`}
              >
                <use href={icons + "#icon-circul"}></use>
              </svg>
              <span>{getUserStatus(id) == 'meeting' ? "In Meeting" : (getUserStatus(id).trim().length && getUserStatus(id) != 'online' ? getUserStatus(id) : (getUserStatus(id) == 'online' ? "Active now" : "Offline"))}</span>
            </div>
            <div className="teammates-list__time">
              <svg className="icon icon-watch">
                <use href={icons + "#icon-watch"}></use>
              </svg>
              <span>{time}</span>
            </div>

            {getUserAppIcon(id) && (
              <div className="teammates-list__document">
                <a
                  href="javascript:void(0)"
                  onClick={() => {
                    goToDoc(id);
                  }}
                >
                  <svg className="icon">
                    <use href={getUserAppIcon(id)}></use>
                  </svg>
                  {/*<span>Google Docs</span>*/}
                </a>
              </div>
            )}

            <button className="teammates-list__button" onClick={(e) => startAudioCall(id)}>
              <svg className="icon icon-talk">
                <use href={icons + "#icon-talk"}></use>
              </svg>
            </button>
            <button className="teammates-list__button" onClick={(e) => startVideoCall(id)}>
              <svg className="icon icon-video">
                <use href={icons + "#icon-video"}></use>
              </svg>
            </button>
            <button
              className={`teammates-list__button`}
              onClick={(e) => {
                startChat(id);
              }}
            >
              <svg className="icon icon-messageFull">
                <use href={icons + "#icon-messageFull"}></use>
              </svg>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
