import React, { useEffect, useState, useRef } from "react";
import Layout from "../../../hoc/Layout/Layout";
import "../Meeting.css";
import icons from "../../../assets/image/icons.svg";
import { EmojiButton } from "@joeattardi/emoji-button";
import DatePicker from "react-datepicker";
import person from "../../../assets/image/other/person.png";
import Header from "../../../components/Header/Header";

function CreateMeeting() {
  const [emojiTitle, setEmojiTitle] = useState(null);
  useEffect(() => {
    const titleEmoji = new EmojiButton({
      position: {
        top: `65px`,
        left: `0px`,
      },
      strategy: "absolute",
      showVariants: true,
      emojiSize: "16px",
      showPreview: false,
    });
    const trigger = document.querySelector("#title-trigger");

    titleEmoji.on("emoji", (selection) => {
      setEmojiTitle(selection.emoji);
    });

    trigger.addEventListener("click", () => {
      titleEmoji.togglePicker(trigger);
    });

    const emojiAll = document.querySelectorAll(".emoji-picker__wrapper");
    for (let emj = 0; emj < emojiAll.length; emj++) {
      emojiAll[emj].id = `emoji${emj}`;
    }
    const emoji = document.querySelector("#emoji1");
    const emojiContent = document.querySelector("#emoji-content");
    emojiContent.appendChild(emoji);

    const allEmoji = document.querySelectorAll(".emoji-picker__emoji");
    for (let i = 0; i < allEmoji.length; i++) {
      allEmoji[i].addEventListener("click", eventClick);
    }
  }, []);

  const [startDate, setStartDate] = useState(new Date());
  const [startTime, setStartTime] = useState(new Date());
  const [startToDate, setStartToDate] = useState(new Date());
  const [startToTime, setStartToTime] = useState(new Date());
  const [open, setOpen] = useState(false);

  const ref = useRef(null);

  useEffect(() => {
    function handleClickOutside(event) {
      if (
        ref.current &&
        !ref.current.contains(event.target) &&
        !event.target.closest(".emoji-picker__wrapper")
      ) {
        setOpen(false);
      }
    }
    document.addEventListener("mouseup", handleClickOutside);
    return () => {
      document.removeEventListener("mouseup", handleClickOutside);
    };
  }, [ref]);

  const eventClick = (e) => {
    e.preventDefault();
  };
  return (
    <Layout>
      <Header />
      <div className="crmeeting">
        <div className="crmeeting-body">
          <div className="crmeeting-head">
            <h1>Create Meeting</h1>
          </div>
          <div className="crmeeting-form">
            <form className="crmeeting-form__body">
              <div className="crmeeting-form__group">
                <label>Title</label>
                <div id="emoji-content" className="crmeeting-form__title">
                  <button
                    id="title-trigger"
                    type="button"
                    className="crmeeting-form__emoji"
                  >
                    {emojiTitle ? (
                      emojiTitle
                    ) : (
                      <svg className="icon icon-emoji">
                        <use href={icons + "#icon-emoji"}></use>
                      </svg>
                    )}
                  </button>
                  <input type="text" placeholder="Add Title" />
                </div>
              </div>
              <div className="crmeeting-form__group">
                <label>Date</label>
                <div className="crmeeting-date">
                  <div className="crmeeting-date__body">
                    <div className="crmeeting-date__container">
                      <div className="crmeeting-date__day crmeeting-date__input">
                        <DatePicker
                          selected={startDate}
                          onChange={(date) => setStartDate(date)}
                        />
                        <svg className="icon icon-down-abgle">
                          <use href={icons + "#icon-down-abgle"}></use>
                        </svg>
                      </div>
                      <div className="crmeeting-date__time crmeeting-date__input">
                        <DatePicker
                          selected={startTime}
                          onChange={(date) => setStartTime(date)}
                          showTimeSelect
                          showTimeSelectOnly
                          timeIntervals={15}
                          timeCaption="Time"
                          dateFormat="h:mm aa"
                        />
                        <svg className="icon icon-down-abgle">
                          <use href={icons + "#icon-down-abgle"}></use>
                        </svg>
                      </div>
                    </div>
                    <span className="crmeeting-date__to">to</span>
                    <div className="crmeeting-date__container">
                      <div className="crmeeting-date__day crmeeting-date__input">
                        <DatePicker
                          selected={startToDate}
                          onChange={(date) => setStartToDate(date)}
                        />
                        <svg className="icon icon-down-abgle">
                          <use href={icons + "#icon-down-abgle"}></use>
                        </svg>
                      </div>
                      <div className="crmeeting-date__time crmeeting-date__input">
                        <DatePicker
                          selected={startToTime}
                          onChange={(date) => setStartToTime(date)}
                          showTimeSelect
                          showTimeSelectOnly
                          timeIntervals={15}
                          timeCaption="Time"
                          dateFormat="h:mm aa"
                        />
                        <svg className="icon icon-down-abgle">
                          <use href={icons + "#icon-down-abgle"}></use>
                        </svg>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="crmedia">
                <div className="crmedia-body">
                  <div className="crmedia-container__group">
                    <span className="crmedia-container__label">Video</span>
                    <div className="crmedia-container__content">
                      <span className="crmedia-container__type">Host</span>
                      <label className="crmedia-container__checkbox">
                        <input type="checkbox" hidden />
                        <span></span>
                      </label>
                    </div>
                    <div className="crmedia-container__content">
                      <span className="crmedia-container__type">
                        Participant
                      </span>
                      <label className="crmedia-container__checkbox">
                        <input type="checkbox" hidden />
                        <span></span>
                      </label>
                    </div>
                  </div>
                  <div className="crmedia-container__group">
                    <span className="crmedia-container__label">Audio</span>
                    <div className="crmedia-container__content">
                      <span className="crmedia-container__type">Host</span>
                      <label className="crmedia-container__checkbox">
                        <input type="checkbox" hidden />
                        <span></span>
                      </label>
                    </div>
                    <div className="crmedia-container__content">
                      <span className="crmedia-container__type">
                        Participant
                      </span>
                      <label className="crmedia-container__checkbox">
                        <input type="checkbox" hidden />
                        <span></span>
                      </label>
                    </div>
                  </div>
                </div>
              </div>
              <div className="crinvite">
                <div className="crinvite-body">
                  <div className="crmeeting-form__group">
                    <label>Invite People</label>
                    <input
                      className="crinvite-body__input"
                      type="text"
                      placeholder="@someone"
                    />
                  </div>
                  <div className="crinvite-persons">
                    <div className="crinvite-persons__item">
                      <img className="image" src={person} alt="Person" />
                      <span className="name">Tural</span>
                      <span className="invite">Invited</span>
                      <button className="delete">
                        <svg className="icon icon-cancel">
                          <use href={icons + "#icon-cancel"}></use>
                        </svg>
                      </button>
                    </div>
                    <div className="crinvite-persons__item">
                      <img className="image" src={person} alt="Person" />
                      <span className="name">Tural</span>
                      <span className="invite">Invited</span>
                      <button className="delete">
                        <svg className="icon icon-cancel">
                          <use href={icons + "#icon-cancel"}></use>
                        </svg>
                      </button>
                    </div>
                    <div className="crinvite-persons__item">
                      <img className="image" src={person} alt="Person" />
                      <span className="name">Tural</span>
                      <span className="invite">Invited</span>
                      <button className="delete">
                        <svg className="icon icon-cancel">
                          <use href={icons + "#icon-cancel"}></use>
                        </svg>
                      </button>
                    </div>
                    <div className="crinvite-persons__item">
                      <img className="image" src={person} alt="Person" />
                      <span className="name">Tural</span>
                      <span className="invite">Invited</span>
                      <button className="delete">
                        <svg className="icon icon-cancel">
                          <use href={icons + "#icon-cancel"}></use>
                        </svg>
                      </button>
                    </div>
                    <div className="crinvite-persons__item">
                      <img className="image" src={person} alt="Person" />
                      <span className="name">Tural</span>
                      <span className="invite">Invited</span>
                      <button className="delete">
                        <svg className="icon icon-cancel">
                          <use href={icons + "#icon-cancel"}></use>
                        </svg>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <div className="crcalendar">
                <div className="crcalendar-body" ref={ref}>
                  <label>Calendar</label>
                  <div className="crcalendar-dropdow">
                    <button
                      className="crcalendar-toggle"
                      type="button"
                      onClick={() => setOpen(!open)}
                    >
                      <span>Google Calendar</span>
                      <svg className="icon icon-down-abgle">
                        <use href={icons + "#icon-down-abgle"}></use>
                      </svg>
                    </button>
                    <div className={`crcalendar-content ${open && "open"}`}>
                      <div
                        className="crcalendar-item"
                        onClick={() => setOpen(!open)}
                      >
                        <span>Google Calendar</span>
                      </div>
                      <div
                        className="crcalendar-item"
                        onClick={() => setOpen(!open)}
                      >
                        <span>Yandex Calendar</span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="crmeeting-form__submit">
                <button>Save</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </Layout>
  );
}

export default CreateMeeting;
