import React, { useState, useEffect } from "react";
import { Route, Switch, Redirect, useHistory } from "react-router-dom";
import { SearchProvider } from "./context/SearchContext";
import { TableProvider } from "./context/TableVariant";
import { ActiveDropdownProvider } from "./context/ActiveDropdownContext";
import { TimeDropdownProvider } from "./context/TimeDropdownContext";

import Home from "./container/Home/Home";
import Notifications from "./container/Notifications/Notifications";
import Auth from "./container/Auth/Auth";
import Login from "./container/Auth/AuthComponents/Login";
import SignIn from "./container/Auth/AuthComponents/SignIn";
import CheckEmail from "./container/Auth/AuthComponents/CheckEmail";
import CompanyName from "./container/Auth/AuthComponents/CompanyName";
import Invite from "./container/Auth/AuthComponents/Invite";
import LoginDetails from "./container/Auth/AuthComponents/LoginDetails";
import Account from "./container/Settings/Account/Account";
import Preferences from "./container/Settings/Preferences/Preferences";
import Sharing from "./container/Settings/Sharing/Sharing";
import Google from "./container/Settings/Integrations/GoogleCalendar";
import Slack from "./container/Settings/Integrations/Slack";
import Chrome from "./container/Settings/Integrations/Chrome";
import Spotify from "./container/Settings/Integrations/Spotify";
import Team from "./container/Settings/Team/Team";
import CreateTeam from "./container/Settings/Team/CreateTeam";
import CreateMeeting from "./container/Meeting/CreateMeeting/CreateMeeting";
import Meeting from "./container/Meeting/Meeting/Meeting";
import Rooms from "./container/Rooms/Rooms";
import Folder from "./container/Folder/Folder";
import Milstone from "./container/Folder/Milstone";
import RoomsComing from "./container/Coming/Rooms";
import MeetindComing from "./container/Coming/Meeting";
import FolderComing from "./container/Coming/Folder"
import * as userService from './api/users';
import * as messagingService from './api/messaging';
import * as requests from './api/requests';


function App(props) {

  let history = useHistory();

 useEffect(() => {
    let token = requests.getToken();
    console.log("token: ", token);

    if(token){
      userService.getAuthUser().then(res => res.json())
                          .then(data => {
                            console.log('user data: ', data);
                            data.token = token;
                            userService.setUserInfo(data);

                            if(data.preferences){
                              userService.setUserPreferences(data.preferences);
                            } else {
                              userService.setUserPreferences({});
                            }

                            if(data.code && data.code == 'NotAcceptable' && data.message == 'tokenExpired'){
                              console.log("redirect..");
                              history.push('/signin');
                            } else {
                              history.push('/dashboard');

                              setTimeout(() => {
                                messagingService.connect();
                              }, 500);
                              
                            }
                          })
                          .catch(err => {
                            console.log('user error: ', err);
                             history.push('/signin');
                          });
                        } else {
                          history.push('/signin');
                        }
    
  }, []);


  return (
    <div className="App">
      <SearchProvider>
        <TableProvider>
        <ActiveDropdownProvider>
        <TimeDropdownProvider>
          <Switch>
            <Route path="/comingfolder" exact component={FolderComing} />
            <Route path="/comingmeeting" exact component={MeetindComing} />
            <Route path="/comingrooms" exact component={RoomsComing} />
            <Route path="/milstone" exact component={Milstone} />
            <Route path="/folder" exact component={Folder} />
            <Route path="/rooms" exact component={Rooms} />
            <Route path="/meeting" exact component={Meeting} />
            <Route path="/meeting/create" exact component={CreateMeeting} />
            <Route path="/settings/createteam" exact component={CreateTeam} />
            <Route path="/settings/team" exact component={Team} />
            <Route path="/settings/spotify" exact component={Spotify} />
            <Route path="/settings/chrome" exact component={Chrome} />
            <Route path="/settings/slack" exact component={Slack} />
            <Route path="/settings/googlecalendar" exact component={Google} />
            <Route path="/settings/sharing" exact component={Sharing} />
            <Route path="/settings/preferences" exact component={Preferences} />
            <Route path="/settings/account" exact component={Account} />
            <Route path="/logindetails" exact component={LoginDetails} />
            <Route path="/invite" exact component={Invite} />
            <Route path="/companyname" exact component={CompanyName} />
            <Route path="/checkemail" exact component={CheckEmail} />
            <Route path="/register" exact component={Login} />
            <Route path="/auth" exact component={Auth} />
            <Route path="/signin" exact component={SignIn} />
            <Route path="/notifications" exact component={Notifications} />
            <Route path="/dashboard" exact component={Home} />
            <Route path="/" exact />
            <Redirect to="/dashboard" />
          </Switch>
          </TimeDropdownProvider>
          </ActiveDropdownProvider>
        </TableProvider>
      </SearchProvider>
    </div>
  );
}

export default App;
